<template>
  <div class="twelve wide column page-body">
    <div class="header">
      <h2 class="ui header">Home</h2>
      <div class="ui section divider"></div>
    </div>
    <template v-for="(data, k) in blogList">
      <div v-bind:key="k" class="content">
        <h1 class="ui header">
          <label v-bind:class="[ lableColor(data.category) ]" class="ui label">
            {{ data.category }}
          </label>
          <a class="black" v-bind:href="`/${data.date}/${data.subtitle}`">{{ data.title }}</a>
        </h1>
        <div class="meta">
          <span class="date">{{ [ data.date, "YYYY/MM/DD" ] | moment("MMMM DD, YYYY") }} / by {{ data.author }}</span>
        </div>
        <div class="description">
          {{ data.preview }}
        </div>
        <div>
          <a href="">Read More</a>
        </div>
      </div>
      <div v-bind:key="'divider_'+k" class="ui section divider"></div>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'Main',
  computed: mapGetters({
    blogList: 'blog/getBlogList',
  }),
  methods: {
    lableColor(cat) {
      return this.$common.findLabel(this.$const, cat);
    },
  },
  created() {
  },
};

</script>

<style scoped>
.header {
  margin-bottom: 0;
}

h1 {
  font-size: 3em;
  font-weight: 700;
}

span.date {
  font-size: 14px;
  line-height: 30px;
  font-weight: 500;
  color: #8c8c8c;
}

.black {
  color: black;
}
</style>



